import { TextField, Typography, Button } from "@mui/material";
import { useFormik } from "formik";
import React from 'react';
const Auth: React.FC = () => {
    const formik = useFormik({
        initialValues: {
            nickname: "",
            password: ""
        },
        onSubmit: ({ nickname, password }) => {
            window.cef.emit("auth-form-submit", [nickname, password]);
        },
    });

    return (
        <form onSubmit={formik.handleSubmit} className="menu">
            <Typography className="menu-title" variant="h4" component="h4">Авторизация</Typography>
            <div className="content">
                <TextField
                    onChange={formik.handleChange}
                    value={formik.values.nickname}
                    name="nickname"
                    className="menu-input"
                    label="Игровой никнейм"
                    variant="filled"
                />
                <TextField
                    onChange={formik.handleChange}
                    value={formik.values.password}
                    name="password"
                    className="menu-input"
                    label="Пароль"
                    variant="filled"
                />
                <Button type="submit" variant="contained">Войти</Button>
            </div>
        </form>
    )
}

export default Auth;